import { useState } from "react";
const Coin = () => {
	const resultFaces = [
		{
			face: "head",
			imgUrl:
				"https://qph.fs.quoracdn.net/main-qimg-9c81a54813716fccd8e3608ab2f51dcf",
		},
		{
			face: "tail",
			imgUrl:
				"https://qph.fs.quoracdn.net/main-qimg-148ae81e6fe0500e130fb547026a9b26",
		},
	];

	const [coinState, setCoinState] = useState([]);
	console.log(coinState);
	function randomResult() {
		const randomIndex = Math.floor(Math.random() * resultFaces.length);
		console.log(randomIndex);
		setCoinState(resultFaces[randomIndex]);
	}
	return (
		<>
			The result is
			{coinState.length === 0 ? (
				""
			) : (
				<div>
					<span>{coinState.face} </span>
					<img alt={coinState.face} src={coinState.imgUrl} />
				</div>
			)}
			<button onClick={randomResult}>flip </button>
		</>
	);
};

export default Coin;
